import React from 'react'
import Message from './message/Message'
import MessageSender from './message-sender/MessageSender'
import s2 from '../../s1-main/App.module.css'
import FriendMessage from './friend-message/FriendMessage'
import avatar from './avatar.png'

/*
* 1 -   MessageType
* 2 -   MessagePropsType   Message.tsx
* 3 -   Message.tsx   
* 4 -   2, 3   FriendMessage.tsx
* 5 -      
* */

//      any
export type MessageType = {
    id: number
    user: User
    message: Message
}

type User = {
    avatar: string
    name: string
}

type Message = {
    text: string
    time: string
}

//    
export const message0: MessageType = {
    id: 1,
    user: {
        avatar: avatar, //  
        name: 'Ivan',  //  
    },
    message: {
        text: 'some new text', //  
        time: '22:00', //  
    },
}
export const friendMessage0: MessageType = {
    id: 100,
    user: {
        avatar: avatar, //  
        name: 'Vasya', //  
    },
    message: {
        text: 'some new text', //  
        time: '22:00', //  
    },
}

const HW1 = () => {
    return (
        <div id={'hw1'}>
            <div className={s2.hwTitle}>Homework #1</div>
            <div className={s2.hw}>
                {/*  ( )*/}
                <div className={s2.hwTopLine}></div>

                <div className={s2.hwContainer}>
                    <div className={s2.messagesContainer}>
                        <Message message={message0} />
                        <FriendMessage message={friendMessage0} />
                    </div>

                    {/*    ( )*/}
                    <MessageSender M={FriendMessage} />
                </div>

                <div className={s2.hwBottomLine}></div>
            </div>
        </div>
    )
}

export default HW1
